/*---------- Home----------*/
    #home{
        position: relative;
        height: 640px;
        background-color: #7510F7;
        background-image: url('../icons/wave.png');
        background-repeat: no-repeat;
        background-position: bottom;
  
    }
  
    #home > div > h1{
        margin-top: 8px; 
        margin-left: 16px; 
        margin-bottom: 8px;
    }

    #home > div > h2{
        font-weight: 400; 
        margin-top: 8px; 
        margin-left: 16px; 
        margin-bottom: 8px;
    }

    #home > div > p{
        width: 720px; 
        margin-top: 8px;
        margin-left: 16px; 
        margin-bottom: 24px;
    }
    
    #header{
        display: flex; 
        flex-direction: row;
    }
    
    #header > nav{
        margin-left: auto; 
        margin-right: 64px;
        align-self: center; 
    }
    
    #header > nav > ul{
        display: inline-block;
        margin-left: 24px; 
        margin-right: 24px;
        font-size: 24px;
        font-weight: 600;
    }
    
    #logo{
        width: auto;
        height: 80px;
        margin: 16px;
    }
/*----------End of Home----------*/