  #projects{
    background: white;
  }

  #projects *{
    color: black;
  }

  #project-item-container{
    display: flex;
    flex-direction: row;
    padding-top: 24px;
    padding-left: 48px;
    padding-right: 48px; 
    padding-bottom: 48px; 
    overflow-x: scroll;
    -ms-overflow-style: none; 
  }

  #project-item-container::-webkit-scrollbar {
    display: none;
  }

  .project-item{
    display: flex;
    flex-direction: row;
    margin-left: 16px;
    margin-right: 16px;
    padding: 64px;
    background: aqua;
    border-radius: 16px;
  }

  .project-item > div > h1{
    font-size: 48px;
    font-weight: 800;
  }

  .project-item > div > h2{
    font-size: 36px;
    font-weight: 800;
  }

  .project-item > div > p{
    width: 640px;
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 24px;
  }

  .project-item > div > ul{
    margin-top: 8px;
    margin-left: 24px;
    margin-right: 16px;
    font-size: 24px;
    font-weight: 300;
  }

  .project-language{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 8px;
    margin-right: 8px;
    padding-top: 8px;
    padding-left: 36px;
    padding-right: 36px;
    padding-bottom: 8px;
    background: white;
    border-radius: 24px;
  }