  #contact{
    height: 780px;
    position: relative;
    background-color: #7510F7;
  }
  
  #contact-form{
    display: flex;
    position: absolute; 
    top: 36px; 
    left: 36px; 
    right: 36px; 
    bottom: 36px; 
    background-color: #141c3a;
    border-radius: 8px;
  }

  #form-left-side{
    width: fit-content;
    height: inherit;
    margin: 16px; 
  }

  #form-left-side > h1{
    margin: 0px 16px 8px 16px;
  }

  #form-left-side > p{
    width: 592px;
    margin: 0px 16px 32px 16px;
  }

  input{
    display: inline-block;
    width: 280px;
    height: 60px;
    margin: 0px 16px 32px 16px;
    padding: 8px;
    font-size: 18px;
    color: black;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
  }

  #form-subject {
    display: block;
    width: 592px;
  }

  textarea {
    display: block;
    width: 592px;
    height: 160px;
    margin: 0px 16px 32px 16px;
    padding: 8px;
    font-size: 18px;
    color: black;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
    resize: none;
  }

  #submit-button{
    width: 592px;
    margin: 0px 16px 32px 16px;
    padding: 16px;
    font-size: 18px;
    color: white;
    background-color: transparent;
    border: solid;
    border-color: #7510F7;
    border-radius: 8px;
    cursor: pointer; 
  }

  #submit-button:hover{
    background-color: #7510F7;
  }

  #form-right-side{
    flex-grow: 1;
    height: inherit;
    background: white;
    border-radius: 8px;
  }