/*----------About me----------*/
  #about{
      background-color: #FFFfFF;
  }

  .grid_container{
    display: grid;
    gap: 8px;
    grid-template-areas:
      'left top'
      'left top'
      'left bottom'
      'left bottom';
    grid-auto-rows: minmax(120px, 120px);
    grid-auto-columns: minmax(240px, 240px);
    margin-right: 36px;
  }

  .grid_container > img{
    width: 240px;
    height: 240px;
    object-fit: cover;
    border-radius: 8px;
  }

  .top{
    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: 2;
  }

  .left{
    grid-column-start: 1;
    grid-row-start: 2;
    grid-row-end: 3;
  }

  .bottom{
    grid-column-start: 2;
    grid-row-start: 3;
    grid-row-end: 4;
  }

  .about_card_container{
    display: flex; 
    flex-direction: row; 
    align-items: center;
    width: fit-content; 
    margin-top: 36px; 
    margin-left: auto; 
    margin-right: auto; 
    
  }

  .about_card{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 280px;
    margin-right: 24px;
    margin-left: 24px;
    padding: 8px;
    background-color: #030405;
    border-radius: 8px;
  }

  .about_card > img{
    width: 48px; 
    height: 48px;
    margin: 8px;
    color: white;
  }

  .about_card > div{
    display: flex;
    flex-direction: column;
    margin-Left: 8px
  }

  .about_card > div > h1{
    color: #7510F7;
    font-size: 22px;
    font-weight: 300; 
  }

  .about_card > div > p{
    margin-top: 4px; 
    color: white;
    font-size: 18px;
    font-weight: 400; 
   
  }

  .interests_container{
    display: flex; 
    flex-direction: row; 
    flex-wrap: wrap; 
    margin-top: 36px; 
    padding: 0px;
  }

  .interests_container > hr{
    flex-grow: 1; 
    align-self: center;
  }

  .interests_container > img{
    align-self: center; 
    width: 48px; 
    height: 48px; 
    margin-left: 16px; 
    margin-right: 16px;
  }

  #interests{
    width: 100%; 
    height: fit-content; 
    padding-top: 8px; 
    padding-bottom: 24px;
  }

  