    #intro{
        text-align: center; 
        margin-top: 16px; 
        margin-bottom: 16px;
    }

  #portfolio{
    position: relative;
    height: 800px;
    padding-top: 36px;
    background-color: #7510F7;
    background-image: url('../icons/wave.png');
    background-repeat: no-repeat;
    background-position: bottom;
  }

  #carousel_container{
    position: relative;
    width: 100%;
    height: 480px;
    margin-top: 36px;
  }

  .prev{
    opacity: 0.8;
    z-index: 1;
    transform-origin: right;
    transform: scale(0.75) translate(0);
  }

  .selected{
    opacity: 1;
    z-index: 2;
    transform-origin: left;
    transform: scale(1) translate(320px);
  }

  .selected button{
    width: fit-content;
    height: fit-content;
    padding-top: 16px;
    padding-left: 36px;
    padding-right: 36px;
    padding-bottom: 16px;
    background-color: transparent;
    border: solid;
    border-color: #7510F7;
    border-radius: 8px;
    cursor: pointer; 
  }

  .selected button:hover{
    background-color: #7510F7;
  }

  .next{
    opacity: 0.8;
    z-index: 1;
    transform-origin: right;
    transform: scale(0.75) translateX(640px);
  }


    #carousel_slider{
        width: fit-content;
        margin-top: 16px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 16px;
    }