#testimonials{
    padding: 16px;
    background: white;
  }
  
  #testimonials *{
    color: black;
  }
  
  
  .item_testimonial{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    width: 400px;
    height: 480px;
    margin: 8px;
    align-self: center;
    border: solid;
    border-color: black;
    border-width: 1px;
    border-radius: 8px;
  }

  .item_testimonial > img{
    border-radius: 50%;
    width: 120px;
    height: 120px;
    margin-top: 36px;
    margin-bottom: 36px;
  }

  .item_testimonial > p{
    text-align: center;
    margin-left: 8px;
    margin-right: 8px;
    margin-bottom: 48px;
  }