@media screen and (min-device-width: 1360px) {
  *{
    margin: 0px;
    padding: 0px;
    font-family: Helvetica Now Display;
    color: white;
    box-sizing: border-box;
  }

  h1{
    font-size: 64px;
    font-weight: 800;
  }

  h2{
    font-size: 48px;
    
  }

  h3{
    font-size: 24px;
  }

  h4{
    font-size: 18px;
  }

  h5{
    font-size: 12px;
    font-weight: 100;
  }

  h6{
    font-size: 10px;
    font-weight: 100;
  }

  p{
    font-size: 22px;
    font-weight: 300;
  }



/*----------Featured Projects----------*/

  #featured-projects{
    height: 640px;
    padding: 16px;
    background: white;
    perspective-origin: top left;
    perspective: 5000px;
  }

  #featured-projects *{
    color: black;
  }

  #project-image-container{
    width: 600px; 
    height: 480px; 
    margin-top: 16px; 
    margin-left: auto;
    margin-right: 8px;
    border-radius: 16px;
    transform: rotateY(-45deg) rotateX(16deg);
    overflow: scroll;
    -ms-overflow-style: none; 
    scrollbar-width: none;  
  }

  #project-image-container::-webkit-scrollbar {
    display: none;
  }

  .project-image{
    width: 568px; 
    height: 440px; 
    margin: 16px;
    background-color: rgb(50, 51, 51);
    object-fit: cover;
    border-radius: 16px;
  }

  .project-dev-tool{
    display: inline;
    width: 40px;
    height: 40px;
    margin-top: 16px;
    margin-right: 16px;
  }

  .project-button{
    margin-top: 16px;
    margin-right: 16px;
    padding: 16px;
    font-size: 18px;
    color: white;
    background-color: #04AA6D;
    border: none;
    border-radius: 4px;
  }

  
/*----------End of Featured Projects----------*/

}